#adminRes_outer {
  display: flex;
  justify-content: center;
}

#adminRes_inner {
  display: flex;
  flex-direction: column;
  justify-content: center;
  border: 2px solid black;
  width: 100%;
  background-color: #fcf5ea;
  padding: 0 2%;
}

#adminRes_heading {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 1% 0;
}

#adminRes_heading h2 {
  margin-bottom: 0;
}

#adminRes_filter {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
  margin-right: 5%;
  margin-bottom: 3%;
}

#adminValues_align {
  margin-bottom: 5%;
  display: flex;
  align-items: center;
  justify-content: center;
}

#adminResrv_filterform {
  display: flex;
  align-items: end;
  justify-content: right;
  margin-right: 5%;
}

.adminReservationheading {
  padding: 8px;
  border: 2px solid black;
  text-align: center;
}

.adminReservationTable {
  padding: 8px;
  border: 2px solid black;
  border-collapse: collapse;
}

.adminResrvfilter_inputs {
  width: 20%;
  margin-right: 1%;
  padding: 1%;
}

#adminResrvfilter_submit {
  padding: 1.3%;
  background-color: aliceblue;
  border: 1px solid black;
  cursor: pointer;
}
